.sections-toggle {
  margin-bottom: 13.9rem;
}

.sections-toggle__list {
  display: flex;
  justify-content: space-between;

  width: 79.5rem;
  .margin-inline(@a);
}

.sections-toggle__btn {
  position: relative;

  .padding-inline(2.5rem);

  font-family: @circe;
  font-weight: 300;
  font-size: 2rem;

  text-transform: uppercase;
  background-color: transparent;
  border: none;

  cursor: pointer;

  &:hover,
  &:focus {
    outline: none;
  }

  &::before {
    content: '';
    position: absolute;

    bottom: -1.2rem;
    left: 0;

    width: 0;
    height: 0.2rem;
    transition-duration: 0.3s;

    background-color: #9BB1FF;

    outline: none;
  }

  &:hover::before,
  &:focus::before {
    width: 100%;
  }
}

.active.sections-toggle__btn {
  font-weight: 700;
  &::before {
    width: 100%;
  }
}

.sections-toggle__title,
.sections-toggle__form {
  display: none;
}

@media (max-width: @mobile-width) {
  .sections-toggle {
    margin-bottom: @m110px;
  }

  .sections-toggle__list {
    display: none;
  }

  .sections-toggle__title,
  .sections-toggle__form {
    display: block;
  }

  .sections-toggle__title {
    margin-bottom: @m46px;
    font-size: @m70px;
    font-weight: 300;
  }

  .sections-toggle__select {
    width: 100%;
    padding-top: @m5px;
    padding-bottom: @m8px;

    font-family: @circe;
    font-weight: 700;
    font-size: @m20px;
    text-transform: uppercase;

    background-color: transparent;
    background-image: url('../img/svg/arrow-bottom-b.svg');
    background-repeat: no-repeat;
    background-position: right @m6px top 50%;
    background-size: @m25px;

    border: none;
    border-radius: 0;
    border-bottom: @m5px solid #9BB1FF;

       -moz-appearance: none;
    -webkit-appearance: none;
            appearance: none;

    outline: none;
  }

}
